@use 'settings' as *;
@use 'breakpoints' as *;
@use 'sass:map';
@use 'sass:math';
@use 'sass:list';


// Funcion para extraer familia, grosor y tamao [ setfont("size", "h0") ]
@function setfont($scheme, $tone: base) {
    @return map.get($fonts, $scheme, $tone);
}

